/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
	/**/
	.sell_right {
		.button {
			margin: 15px auto;
			float: none !important;
			max-width: 250px; } }

	.sell_descr {
		text-align: center; }

	.main_mnu {
		ul {
			background-color: #3797bf;
			display: none;
			position: absolute;
			width: 100%;
			margin-top: 5px;
			li {
				display: block; } } } }

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
	/**/
	.top_links {
		background-color: #585d61;
		display: none;
		margin-top: 15px;
		padding: 14px;
		position: absolute;
		vertical-align: middle;
		width: 100%; } }



