@import "fonts";
@import "libs";
@import "vars";

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; } }

*::-webkit-input-placeholder {
	color: #666;
	opacity: 1; }

*:-moz-placeholder {
	color: #666;
	opacity: 1; }

*::-moz-placeholder {
	color: #666;
	opacity: 1; }

*:-ms-input-placeholder {
	color: #666;
	opacity: 1; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	border: 3px solid red; }
body input:required:valid,
body textarea:required:valid {
	border: 3px solid green; }

html, body {
	height: 100%; }

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.75;
	font-family: $default-font;
	overflow-x: hidden;
	opacity: 1; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }

//Header styles

.top_links {
	ul {
		margin: 0;
		padding: 0;
		list-style-type: none;
		float: left;
		li {
			display: inline-block; } } }


#articles-table {
	a {
		cursor: pointer; } }



@import "media"; //   
